summary {
  text-align: center;
  font-size: 1.25rem;
  font-weight:500;
  text-align: center;
}
summary:hover {
  color: rgb(220,30,50);  
}
details summary::marker {
  content: url('https://api.iconify.design/ic:baseline-keyboard-arrow-right.svg');
}
details[open] summary::marker {
  color: rgb(220, 30, 50);
  content: url('https://api.iconify.design/ic:baseline-keyboard-arrow-down.svg?color=rgb(220,30,50)');
}
a:link {
  color: var(--solitadgray);
  display: block;
  text-align: center;
  margin: 0.5rem;
  padding: 0.5rem;
  text-decoration: none;
}
a:visited {
  color: var(--solitamgray);
}
a:hover {
  text-decoration: solid underline;
  text-decoration-color: rgb(220, 30, 50);
}

.topelement {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  backdrop-filter: blur(2rem);
  -webkit-backdrop-filter: blur(2rem);
  z-index: 5;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}

.header h2 {
  color: var(--solitared);
}

.footer {
  position: fixed;
  bottom: 0;
  width: 80%;
  backdrop-filter: blur(2rem);
  -webkit-backdrop-filter: blur(2rem);
  z-index: 5;
}

.column {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.navigator {
  justify-items: center;
  align-items: center;
  width: 30%;
}

.viewer {
  display: block;
  position:relative;
  top: 6rem;
}

.data {
  margin: 0.5rem;
  padding: 0.5rem;
}

.data .element {
  margin: 0.5rem 0.5rem 0.5rem 1.65rem;
  padding: 0 0.5rem;
}

.data .element:focus {
  outline: none;
}

.data .element:first-child {
  margin: 0 0.5rem;
  padding: 0 0.5rem;
}

.data button.element {
  margin: 0 0.5rem 0.5rem 2rem;
}

.highlight {
  outline: none;
  border: none;
}

.highlight:hover {
  box-shadow: 2px 2px 2px rgba(220,30,50,0.75);
}

.set {
  margin: 0.5rem 1rem;
}

.set-container {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

.small-subset {
  height: 5rem;
  max-width: 24rem;
  margin: 0.5rem;
  padding: 1rem;
}
.subset {
  height: 7rem;
  max-width: 24rem;
  margin: 0.5rem;
  padding: 1rem;
}
.large-subset {
  height: 9rem;
  max-width: 24rem;
  margin: 0.5rem;
  padding: 1rem;
}
.subset:hover, .small-subset:hover, .large-subset:hover {
  outline: 1px solid rgb(220, 30, 50);
}

.district {
  height: 18rem;
  width: 20rem;
  max-width: 24rem;
  margin: 0.5rem;
  padding: 1rem;
}
.district:hover {
  outline: 1px solid rgb(220, 30, 50);
}

.dataVis {
  margin: 0.5rem;
  padding: 0.5rem;
}